import { T1Component } from '../../components/numerical-methods/t1/t1.component';
import { RouterModule } from '@angular/router';
import { ROUTES } from './numerical-methods.routing';
import { CommonImportsModule } from '../common-imports/common-imports.module';

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(ROUTES),
    CommonImportsModule
  ],
  declarations: [TutoringoneComponent, T1Component]
})
export class NumericalMethodsModule { }



