import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import Swal from 'sweetalert2'
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import { MatTableDataSource } from '@angular/material';
@Component({
  selector: 'app-t1',
  templateUrl: './t1.component.html',
  styleUrls: ['./t1.component.css']
})
export class T1Component implements OnInit, AfterViewInit {

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.newtonRaphsonMethod(1, 50, this.equationc, this.equationcDerivative, 'resultadoEjercicioC')
    this.newtonRaphsonMethod(1, 100, this.equationd, this.equationdDerivative, 'resultadoEjercicioD')
    }, 3000);
  }
  title = 'ut';

  public multi: any[] = [];

  public view: any[] = [window.screen.width - 300, window.screen.height - 500];

  // options
  showXAxis = true;
  showYAxis = true;
  gradient = false;
  showLegend = true;
  showXAxisLabel = true;
  xAxisLabel = 'X';
  showYAxisLabel = true;
  yAxisLabel = 'Y';

  colorScheme = {
    domain: ['#5AA454', '#A10A28', '#C7B42C', '#AAAAAA']
  };

  // line, area
  autoScale = true;
  initDataForm: FormGroup;
  formXi: FormGroup;
  resultFormXi: string;
  xexcersiceb: FormControl = this.fb.control(null, [Validators.required]);
  xexcersicea: FormControl = this.fb.control(null, [Validators.required]);
  xexcersicec: FormControl = this.fb.control(null, [Validators.required]);
  xexcersicecDerivate: FormControl = this.fb.control(null, [Validators.required]);
  xexcersiced: FormControl = this.fb.control(null, [Validators.required]);
  xexcersicedDerivate: FormControl = this.fb.control(null, [Validators.required]);
  resultExcersicea: number = null;
  resultExcersiceb: number;

  public nameexcersicea: string = '"e^(x-1)-1.5x"';
  public nameexcersiceb: string = "sen(x)-2x+1";
  public nameexcersicec: string = "cox(x)-x^2";
  public nameexcersicecDerivative: string = "-sin(x)-2x";
  public nameexcersiced: string = "2x^2+1-e^x";
  public nameexcersicedDerivative: string = "4x-e^x";


  private data = [
    {
      title: 'Ejercicio A',
      subtitle: this.nameexcersicea,
      control: this.xexcersicea,
      id: 'ejercicioa',
      equation: this.equationa
    },
    {
      title: 'Ejercicio B',
      subtitle: this.nameexcersiceb,
      control: this.xexcersiceb,
      id: 'ejerciciob',
      equation: this.equationb
    },
    {
      title: 'Ejercicio C',
      subtitle: this.nameexcersicec,
      control: this.xexcersicec,
      id: 'ejercicioc',
      equation: this.equationc
    },
    {
      title: 'Ejercicio C Derivada',
      subtitle: this.nameexcersicecDerivative,
      control: this.xexcersicecDerivate,
      id: 'ejerciciodDerivative',
      equation: this.equationcDerivative
    },
    {
      title: 'Ejercicio D',
      subtitle: this.nameexcersiced,
      control: this.xexcersiced,
      id: 'ejerciciod',
      equation: this.equationd
    },
    {
      title: 'Ejercicio D Derivada',
      subtitle: this.nameexcersicedDerivative,
      control: this.xexcersicedDerivate,
      id: 'ejerciciodDerivative',
      equation: this.equationdDerivative
    }
  ]


  /* tables data */

  public tablea: MatTableDataSource<any>
  public tableb: MatTableDataSource<any>
  constructor(
    private fb: FormBuilder
  ) {

  }
  displayedColumnsBisection = ['id', 'xi', 'xu', 'ea', 'xr', 'fxr'];
  ngOnInit() {
    this.data.forEach(item => this.loadxExcersice(item.control, item.id, item.equation))
    this.buildInitDataForm();
    this.buildFormXi();
    this.tablea = new MatTableDataSource((this.bisectionMethod(2, 3, 0, this.equationa)))
    this.tableb = new MatTableDataSource((this.bisectionMethod(2, 3, 0, this.equationa)))
    this.bisectionMethod(0, 1, 0, this.equationb)
    this.bisectionMethod(0, 1, 0, this.equationc)
    
    this.loadBiseccionExcersice(this.initDataForm.value)
  }


  loadTablea(rounds: IRound[]) {
    this.tablea = new MatTableDataSource(rounds);

  }

  loadxExcersice(xexcersice: FormControl, ref: string, equation: Function): any {
    xexcersice
      .valueChanges
      .debounceTime(500)
      .distinctUntilChanged()
      .subscribe(x => {
        if (typeof x == 'number') {
          const val = equation(x)
          document.getElementById(ref).innerHTML = val + ''
        } else {
          document.getElementById(ref).innerHTML = `
          <div class="alert alert-danger" role="alert">
            Solo se aceptan datos <strong>numericos</strong>
          </div>
          `
        }
      })
  }

  buildInitDataForm() {
    this.initDataForm = this.fb.group({
      fromTo: this.fb.group({
        from: [0, Validators.required],
        to: [2, Validators.required]
      }),
      excersice: this.fb.group({
        a: [true],
        b: [true],
        c: [true],
        d: [true]
      })
    })
  }

  private buildFormXi() {
    this.formXi = this.fb.group({
      a: [null, Validators.required],
      b: [null, Validators.required]
    })


    this.formXi
      .valueChanges
      .debounceTime(500)
      .distinctUntilChanged()
      .subscribe(val => {
        if (this.formXi.valid) {
          const result: number = (val.a + val.b) / 2
          this.resultFormXi = `${val.a} + ${val.b} = ${result}`;
        }
      })
  }

  loadBiseccionExcersice(formData) {
    this.multi = [];
    const excersice = formData.excersice;
    if (excersice.a) this.excersice(formData.fromTo, this.equationa, this.nameexcersicea);
    if (excersice.b) this.excersice(formData.fromTo, this.equationb, this.nameexcersiceb);
    if (excersice.c) this.excersice(formData.fromTo, this.equationc, this.nameexcersicec);
    if (excersice.d) this.excersice(formData.fromTo, this.equationd, this.nameexcersiced);
  }

  excersice(fromTo: IFromTo, funct: Function, name: string) {
    const equation = {
      "name": name,
      "series": []
    }


    let i = fromTo.from
    while (i <= fromTo.to) {
      const val = funct(i)
      equation.series.push({

        "name": i,
        "value": val
      })
      i += 0.001
    }

    this.multi.push(equation);
  }



  onSelect(event) {
    console.log(event);
  }

  equationa(x: number) {
    return Math.pow(Math.E, (x - 1)) - 1.5 * x
  }

  equationb(x: number) {
    return Math.sin(x) - (2 * x) + 1
  }

  equationc(x: number) {
    return Math.cos(x) - Math.pow(x, 2)
  }

  equationcDerivative(x: number) {
    return -1 * Math.sin(x) - 2 * x
  }

  equationd(x: number) {
    return 2 * Math.pow(x, 2) + 1 - Math.pow(Math.E, x)
  }

  equationdDerivative(x: number) {
    return 4 * x - Math.pow(Math.E, x)
  }

  private xi(a: number, b: number): number {
    const val = (a + b) / 2;
    return val
  }

  private errora(xNew: number, xPrevious?: number): number {
    if (!xPrevious) return
    const calc: number = (xNew - xPrevious) / xNew;
    return Math.abs(calc) * 100
  }

  private erroraAlternative(xu: number, xi: number): number {
    const calc: number = (xu - xi) / (xu + xi)
    return Math.abs(calc) * 100
  }

  private bisectionMethod(minInterval: number, maxInterval: number, errorAdmit: number, equation: Function, maxRounds?: number): IRound[] {
    const rounds: IRound[] = [];
    let i: number = 0;
    const initRound = this.bisectionRound(minInterval, maxInterval, equation)
    rounds.push(initRound)
    while (true) {
      const greaterThan0: IRound[] = rounds.filter(round => { if (round.fxr > 0) return round })
      const lowerThan0: IRound[] = rounds.filter(round => { if (round.fxr < 0) return round })
      const b: number = greaterThan0[greaterThan0.length - 1] ? greaterThan0[greaterThan0.length - 1].xr : (equation(minInterval) > 0) ? minInterval : maxInterval;
      const a: number = lowerThan0[lowerThan0.length - 1] ? lowerThan0[lowerThan0.length - 1].xr : (equation(minInterval) < 0) ? minInterval : maxInterval;

      const round: IRound = this.bisectionRound(a, b, equation, rounds[rounds.length - 1].xr);
      if (!round) {
        console.log("Algo salio mal en el round ", rounds.length + 1)
        rounds.splice(0, -1);
        return []
      }
      rounds.push(round);
      i++;

      if (maxRounds === i || rounds[rounds.length - 1].ea <= errorAdmit) break;
    }
    return rounds
  }

  private newtonRaphsonMethod(xi: number, maxRounds: number, equation: Function, equationDerivative: Function, id: string) {
    const rounds = [];

    let i: number = 0;

    while (i <= maxRounds) {
      const fx: number = equation(xi)
      const fprimex: number = equationDerivative(xi)
      if (fprimex == 0) return console.log("fprima es igual a 0")
      const xi_1: number = this.newtonRaphson(xi, fx, fprimex);
      if (xi == xi_1) return document.getElementById(id).innerHTML = `El resultado es: ${xi} cantidad de vueltas ${i}`
      xi = xi_1;

      i++;
    }

    console.log(id)
    document.getElementById(id).innerHTML = `El resultado es: ${xi} cantidad de vueltas ${i}`
    
  }

  private newtonRaphson(xn: number, fx: number, fprimex: number): number {
    return xn - (fx / fprimex);
  }



  private bisectionRound(minInterval: number, maxInterval: number, equation: Function, xPrevious?: number): IRound {

    const a = equation(minInterval);
    const b = equation(maxInterval);

    if (!this.isMaxMin0(a, b)) return;

    const xi: number = a;
    const xu: number = b;
    const xr: number = this.xi(minInterval, maxInterval);
    const ea: number = this.errora(xr, xPrevious);
    const fxr: number = equation(xr);
    return { xi, xu, xr, ea, fxr }
  }

  private isMaxMin0(a: number, b: number): boolean {
    return (a < 0 && b > 0) || (a > 0 && b < 0)
  }


}


export interface IRound {
  i?: number;
  xi: number;
  xu: number;
  xr: number;
  ea: number;
  fxr: number
}

export interface IFromTo {
  from: number;
  to: Number
}



